import { Component, OnInit } from '@angular/core';
import { ImageService } from "src/app/_services";
@Component({
  selector: 'app-happy-client',
  templateUrl: './happy-client.component.html',
  styleUrls: ['./happy-client.component.css']
})
export class HappyClientComponent implements OnInit {

  testimonials: any = [];
  testimonial: any = {};
  slideIndex = 0;
  constructor( private imageService: ImageService,) {
   
   }

  ngOnInit(): void { 

  this.testimonials = [
    {
      description:
        "The best part of UrSpayce is  about the digital infusion in the admin department. We are able to manage all 36 offices from one single dashboard. By Using UrSpayce we are able to provide a seamless experience to our employees and visitors. ",
      name: "Dilip Ranjitmal Lodha",
      title: "STL is an industry-leading integrator of digital networks.",
      post: "Associate Manager",
      logo: "/home/sterlite.png",
    },
    {
      description:
        "If you are running a coworking space than we highly recommend UrSpayce SAAS platform. We now manage, market and monetize through UrSpayce. A technology which every workplace must have.",
      name: "Pradeep Agrawal",
      title: "InstaOffice is leading Coworking space from India.",
      post: "COO",
      logo: "/home/insta-office.png",
    },
  ];
  this.testimonial = this.testimonials[this.slideIndex];
}
  
  slideLeft() {
    if (this.slideIndex > 0) {
      this.slideIndex--;
      this.testimonial = this.testimonials[this.slideIndex];
    }
  }
  slideRight() {
    if (this.slideIndex < this.testimonials.length - 1) {
      this.slideIndex++;
      this.testimonial = this.testimonials[this.slideIndex];
    }
  }
  getStaticImage(name) {
    return this.imageService.getStaticImage(name);
  }

}
