import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { appConfig } from "src/app/global.config";
@Component({
  selector: 'app-free-location-box',
  templateUrl: './free-location-box.component.html',
  styleUrls: ['./free-location-box.component.css']
})
export class FreeLocationBoxComponent implements OnInit { hostUrl = appConfig.HOST;
  dashboardUrl = appConfig.dashboardUrl;
  cta = "home";
  constructor(
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.cta = this.router.url.replace("/", "");
  }

}
