import { Component, OnInit } from '@angular/core';
import { ImageService } from "src/app/_services";
import { Router } from '@angular/router';
import { appConfig } from "src/app/global.config";
import { CryptoService, LocationService } from "src/app/_services";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ZohoService } from 'src/app/_services/zoho.service';
@Component({
  selector: 'app-button-groupbox',
  templateUrl: './button-groupbox.component.html',
  styleUrls: ['./button-groupbox.component.css']
})
export class ButtonGroupboxComponent implements OnInit {   hostUrl = appConfig.HOST;
  dashboardUrl = appConfig.dashboardUrl;
  cta = "home";
  zohoRegister: FormGroup;

  constructor(private imageService:ImageService ,private router: Router,private formBuilder: FormBuilder,
    private zohoService: ZohoService,
    private cryptoService: CryptoService,) {            
   }

  ngOnInit(): void {
    this.zohoRegister = this.formBuilder.group({
      email: ["", [Validators.required, Validators.email]]
    })
    this.cta = this.router.url.replace("/", "");
  }
  getStaticImage(name) {
    return this.imageService.getStaticImage(name);
   
  } 
  async registerZoho() {
    if (!this.zohoRegister.value.email) { return; }



    this.zohoService.registerZoho(this.zohoRegister.value).subscribe(async (res) => {
      console.log(this.zohoRegister.value.email);

      let data = await this.cryptoService.encryptf(this.zohoRegister.value.email);
      // admin-url for staging andn prod required
      // window.open('http://localhost:4300/'+ 'register' + `?cta=menu&email=${encodeURIComponent(data)}`, "_blank");

      window.location.href = this.dashboardUrl + 'register' + `?cta=menu&email=${encodeURIComponent(data)}`, '_blank';

    }, err => {
      console.log(err);
      ;
    });
  }
}
